@import nulstyle

body
    color: #272727
.container
    max-width: 1200px
    width: 100%
    margin: 0 auto
    padding: 0 20px 0 20px
header
    max-width: 1920px
    width: 100%
.header__top
    display: flex
    justify-content: space-between
    align-items: center
    text-align: center
    padding-top: 40px
.header__top-links
    display: flex
    align-items: center
    padding-left: 10px
.header__top-link
    padding-right: 40px
    color: #B8BECD
    transition: all .3s
    font-weight: 700
    font-size: 16px
    line-height: 19px
    padding-bottom: 20px
    padding-left: 20px
    &:hover
        color: #000000
@media ( max-width: 850px )
    .header__top-link
        padding-left: 0px
    .header__top
        flex-wrap: wrap
        justify-content: normal
.header__top-button
    color: #FB8F1D
    font-weight: 500
    font-size: 15px
    line-height: 60px
    border: 1px solid #FB8F1D
    padding: 10px 45px
    border-radius: 8px
    transition: all .3s
    &:active
        background-color: #FB8F1D
        color: #fff
.header__top-button:not(:last-child)
    margin-right: 18px
.header__sub
    display: flex
    justify-content: space-between
.header__sub-left
    padding-top: 114px
    z-index: 10
    max-width: 100%
    height: auto
    display: block
@media ( max-width: 1000px )
    .header__sub-right
        position: relative
        margin-right: auto
        margin-left: auto
        opacity: 50%
    .header__sub-left
        padding-top: 70px
        text-align: center
        position: absolute
        top: 50%
        left: 50%
        margin-right: -50%
        transform: translate(-50%, -50%)
    .line
        display: none
    .left__sub-title
        border-top: 1px solid #222
        padding-top: 30px
@media ( max-width: 600px )
    .header__sub-left
        padding-top: 0px
        margin-top: -50px
.left__title
    font-size: 64px
    line-height: 80px
    padding-bottom: 74px
    font-weight: 800
@media ( max-width: 700px )
    .left__title
        font-size: 40px
        padding-bottom: 30px
.left__sub-title
    font-weight: 600
    font-size: 22px
    line-height: 27px
.line
    border-bottom: 2px solid #272727
    width: 32px
    padding: 21px 0 0 0
    margin-bottom: 40px

.right-img
    max-width: 100%
    height: auto
    display: block
    z-index: 0
